:root {
  --primary-color: #4666b1;
  --main-text-color: #fff;
  --dropdown-text-color: #616161;
  --dropdown-border-color: #cecece;
  --dropdown-background: #fff;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-size: 16px;
  font-family: "Helvetica Neue", sans-serif;
}

/* CAlculator */
.calculator-wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 1200px;
  margin: 10px auto;
  background-color: #efefef;
  padding: 40px;
  border-radius: 10px;
  border: 1px solid #bfbfbf;
}
.calculator-wrapper section {
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  text-align: left;
}
.calculator-wrapper section p {
  margin-bottom: 10px;
}
.calculator-header {
  font-size: 30px;
  margin-bottom: 20px;
}
.calculator-container {
  width: 100%;
  display: flex;
  margin-bottom: 20px;
}
.transport-type ul li {
  display: inline;
  margin-right: 10px;
  padding: 5px 15px;
  cursor: pointer;
  border-radius: 14px;
}
.transport-type .active {
  background-color: red;
  color: white;
}
.calculator-wrapper .total-price {
  font-weight: 800;
  text-align: left;
  width: 100%;
  font-size: 24px;
}
.calculator-wrapper .dop-factors input {
  display: none;
}
.calculator-wrapper .dop-factors label {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
}
.calculator-wrapper .dop-factors label:before {
  content: "";
  width: 30px;
  height: 30px;
  display: flex;
  margin-right: 10px;
  background-color: #fff;
  border-radius: 4px;
  color: white;
  font-weight: bold;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  border: 2px solid #bbb;
}
.calculator-wrapper .dop-factors input:checked + label:before {
  content: "✔︎";
  color: #fff;
  background-color: red;
  border-color: red;
}
.calculator-wrapper .distance {
  display: flex;
  flex-direction: column;
}
.calculator-wrapper .distance label {
  font-size: 20px;
  font-weight: 800;
}
.calculator-wrapper .distance input {
  -webkit-appearance: none;
  appearance: none;
  background: white;
  cursor: pointer;
  width: 15rem;
  width: 100%;
  border: none;
  border-radius: 8px;
  height: 20px;
  margin: 20px 0;
  border: 1px solid #bbb;
}
.calculator-wrapper .distance input::-webkit-slider-thumb {
  -webkit-appearance: none; /* Override default look */
  appearance: none;
  margin-top: -12px; /* Centers thumb on the track */
  background-color: red;
  height: 30px;
  width: 15px;
  transform: translateY(5px);
  border-radius: 15px;
}
.blocked-wheels {
  display: flex;
  width: 100%;
}

.blocked-wheels button {
  width: 40px;
  height: 40px;
}
.blocked-wheels input {
  height: 40px;
  text-align: center;
  font-size: 20px;
  padding: 5px 10px;
}
.blocked-wheels input::-webkit-outer-spin-button,
.blocked-wheels input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.calculator-wrapper .note {
  text-align: left;
  margin: 20px 0;
  font-size: 16px;
}
/* CONTACT FORM */
.contact-form-wrapper {
  display: flex;
  width: 100%;
  max-width: 1200px;
  align-items: center;
  margin: 10px auto;
  background-color: #738083;
  border-radius: 10px;
  border: 1px solid #bfbfbf;
  color: #fff;
  padding: 15px 15px 0 40px;
  gap: 40px;
}
.contact-form-wrapper .form-container {
  padding-bottom: 40px;
  padding-top: 20px;
}
.contact-form-wrapper div {
  width: 100%;
}
.contact-form-wrapper h2 {
  font-size: 24px;
  margin-bottom: 20px;
}
.contact-form-wrapper input {
  width: 100%;
  border: none;
  outline: none;
  height: 50px;
  margin-bottom: 20px;
  font-size: 20px;
  padding: 5px 10px;
  border-radius: 4px;
}

.contact-form-wrapper .btn {
  background-color: red;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  cursor: pointer;
  padding: 10px;
  border-radius: 8px;
}
.politika-wrapper {
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: flex-start;
}

.politika-wrapper .error {
  color: red;
  font-size: 16px;
  margin-bottom: 20px;
}
.politika-wrapper input {
  display: none;
}
.politika-wrapper label:before {
  content: "";
  width: 30px;
  height: 30px;
  display: flex;
  margin-right: 10px;
  background-color: #fff;
  border-radius: 4px;
  color: white;
  font-weight: bold;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  border: 2px solid #bbb;
}
.politika-wrapper label {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  text-align: left;
}
.politika-wrapper input:checked + label:before {
  content: "✔︎";
  color: #fff;
  background-color: red;
  border-color: red;
}
.politika-wrapper a {
  color: #fff;
}
.contact-form-wrapper input::-webkit-outer-spin-button,
.contact-form-wrapper input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.callBack__img {
  height: 287px;
  background: url(./assets/img/operator.webp) left bottom no-repeat,
    url(./assets/img/truck.webp) center no-repeat;
  background-size: contain;
}
/* MENU */
nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}
.logo {
  width: 150px;
  cursor: pointer;
}
.info {
  width: calc(100% - 350px);
  padding-left: 30px;
}
.menu {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.menu ul {
  display: flex;
}
.menu ul li {
  list-style: none;
  margin-right: 20px;
  padding: 10px;
  cursor: pointer;
  border-bottom: 3px solid transparent;
}
.menu ul li:hover {
  border-color: red;
}
.contacts {
  font-size: 18px;
  font-weight: 800;
  width: 200px;
}
.current-district {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.current-district i {
  font-size: 20px;
  color: red;
}

.select-dropdown:last-child {
  margin-right: 0px;
}

.select-dropdown__button {
  padding: 10px 35px 10px 15px;
  background-color: transparent;
  color: red;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  width: 210px;
  text-align: left;
  font-size: 16px;
}

.select-dropdown__button:focus {
  outline: none;
}

.zmdi-chevron-down {
  position: absolute;
  right: 10px;
  top: 12px;
}

.select-dropdown__list {
  position: absolute;
  display: block;
  left: 0;
  top: 45px;
  overflow: auto;
  margin: 0;
  padding: 0 40px;
  list-style: none;
  opacity: 0;
  pointer-events: none;
  transform-origin: top left;
  transform: scale(1, 0);
  transition: all ease-in-out 0.3s;
  z-index: 2;
  background-color: #fff;
  width: 340px;
  text-align: left;
}
.select-dropdown__list li {
  list-style: none;
  color: red;
  text-decoration: underline;
  cursor: pointer;
  margin-bottom: 10px;
}
.select-dropdown__list.active {
  opacity: 1;
  pointer-events: auto;
  transform: scale(1, 1);
}

.select-dropdown__list-item {
  display: block;
  list-style-type: none;
  padding: 10px 15px;
  background: var(--dropdown-background);
  border-top: 1px solid var(--dropdown-border-color);
  font-size: 14px;
  line-height: 1.4;
  cursor: pointer;
  color: #616161;
  text-align: left;
  transition: all ease-in-out 0.3s;
}

.select-dropdown__list-item:hover,
.select-dropdown__list-item.selected {
  background-color: var(--primary-color);
  color: var(--main-text-color);
}

.select-dropdown {
  position: relative;
  max-width: 100%;
  margin: 0 15px 0 0;
}

/* TABLE */
.price-table-header {
  margin: 60px 0 40px;
  font-size: 34px;
}
table {
  width: 100%;
  max-width: 1200px;
  margin: 20px auto;
  border-collapse: collapse;
  font-size: 18px;
}
thead {
  background-color: red;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
}
tr {
  height: 40px;
}
td {
  padding: 5px 15px;
}
td:nth-child(odd) {
  text-align: left;
}
td:nth-child(even) {
  width: 160px;
}
tr:nth-child(even) {
  background-color: #eee;
}

/* FOOTER */
.footer-wrapper {
	width: 100%;
	background-color: #333;
	color: #fff;
	padding: 40px 0;
	margin-top: 40px;
	display: flex;
	flex-direction: column;
	justify-content: center;
  align-items: center;
}
.footer-container {
	display: flex;
	justify-content: space-between;
  align-items: flex-start;
	width: 100%;
	max-width: 1200px;
	gap: 20px;
}
/* .contact-info, .mo-regions, .m-districts, .more-info {
	width: 100%;
} */
.footer-container .title {
	font-size: 20px;
	text-transform: uppercase;
	margin-bottom: 15px;
	color: #bbb;
}
.m-districts li {
	list-style: none;
	text-align: left;
}
.copyright {
	margin-top: 20px;
}
@media (max-width: 400px) {
  nav {
    flex-direction: column;
  }
  .info {
    width: 100%;
    padding: 0 10px;
  }
  .current-district {
    justify-content: center;
  }
  .select-dropdown__button,
  .select-dropdown__list {
    text-align: center;
  }
  .menu ul {
    flex-wrap: wrap;
    justify-content: center;
  }
  .menu ul li {
    margin-right: 6px;
    padding: 7px;
  }
  .calculator-wrapper {
    padding: 25px;
    border-radius: 0;
    border: none;
  }
  .calculator-container,
  .calculator-wrapper .distance {
    margin-bottom: 0;
  }

  .transport-type ul {
    display: flex;
    flex-wrap: wrap;
  }
  .transport-type ul li {
    margin-right: 0;
  }
  .contact-form-wrapper {
    flex-direction: column;
    padding: 15px;
  }
  .calculator-wrapper .dop-factors label:before {
    width: 20px;
    height: 20px;
  }
	.footer-wrapper  {
		padding: 25px;
	}
	.footer-container {
		flex-direction: column;
		justify-content: center;
    align-items: center;
	}
}
